import { mapGetters } from 'vuex';
export default {
  name: 'Footer',
  computed: {
    ...mapGetters("base", ['storeUserConfig']),
    footerLink() {
      return this.storeUserConfig?.footerLink || 'https://github.com/jongsh';
    },
    thisYear() {
      return new Date().getFullYear();
    },
    disableFooter() {
      return this.storeUserConfig?.disableFooter || false;
    }
  }
};