/**
 * 背景图管理 Mixin
 * 用于统一管理页面背景图的加载和轮播逻辑
 */

import { mapGetters } from "vuex";
import debounce from "@/utils/debounce";

export default {
  data() {
    return {
      bingWallPaperIndex: 0,
      customWallPaperIndex: 0,
      backgroundInterval: null, // 存储轮播定时器
      backgroundInitParams: null, // 存储初始化参数，用于主题切换时重新初始化
      isChanging: false, // 防止重复修改配置导致多次初始化
    };
  },
  computed: {
    ...mapGetters("base", ["storeUserConfig", "storeBingWallPapers", "storeUseDarkMode"]),
    useDarkMode() {
      return this.storeUseDarkMode || document.documentElement.classList.contains("dark");
    },
    bkInterval() {
      return this.storeUserConfig?.bkInterval || 3000;
    },
    bkOpacity() {
      return this.storeUserConfig?.bkOpacity || 1;
    },
    // 提供背景图片元素的模板
    backgroundImagesTemplate() {
      return `
        <img id="bg1" class="background-image1" alt="Background Image"/>
        <img id="bg2" class="background-image2" alt="Background Image"/>
      `;
    },
    // 提供背景图片的CSS样式
    backgroundImagesStyles() {
      return `
        .background-image1, .background-image2 {
          position: fixed;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          object-fit: cover;
          z-index: -1;
          opacity: 0;
          transform: scale(1);
          transition: opacity 0.8s cubic-bezier(0.42, 0, 0.58, 1), transform ${
            this.bkInterval / 1000
          }s cubic-bezier(0.42, 0, 0.58, 1);
        }
      `;
    },
  },

  watch: {
    // 监听深色模式切换
    useDarkMode: debounce(async function (newVal, oldVal) {
      if (newVal !== oldVal && this.backgroundInitParams && !this.isChanging) {
        await this.smoothThemeTransition(
          this.backgroundInitParams.configKey,
          this.backgroundInitParams.containerSelector,
          this.backgroundInitParams.useDefaultBackground
        );
        this.isChanging = false; // 设置标志，防止重复触发
      }
    }, 300),
  },

  mounted() {
    // 动态注入背景图片的CSS样式
    this.injectBackgroundStyles();
  },

  beforeUnmount() {
    // 组件销毁前清除定时器
    if (this.backgroundInterval) {
      clearInterval(this.backgroundInterval);
    }
    // 立即清除背景图片，不使用过渡效果
    this.clearBackgroundImages(true);
    // 移除动态注入的样式
    this.removeBackgroundStyles();
  },

  methods: {
    /**
     * 动态注入背景图片样式
     */
    injectBackgroundStyles() {
      const styleId = "background-manager-styles";
      if (!document.getElementById(styleId)) {
        const style = document.createElement("style");
        style.id = styleId;
        style.textContent = this.backgroundImagesStyles;
        document.head.appendChild(style);
      }
    },

    /**
     * 移除动态注入的样式
     */
    removeBackgroundStyles() {
      const styleId = "background-manager-styles";
      const existingStyle = document.getElementById(styleId);
      if (existingStyle) {
        existingStyle.remove();
      }
    },

    /**
     * 动态创建背景图片元素
     * @param {HTMLElement} container - 要插入背景图片的容器元素
     */
    createBackgroundElements(container) {
      if (!container) {
        console.warn("未提供有效的容器元素");
        return;
      }
      // 检查是否已经存在背景图片元素
      if (document.getElementById("bg1") || document.getElementById("bg2")) {
        return;
      }
      // 创建 bg1 元素
      const bg1 = document.createElement("img");
      bg1.id = "bg1";
      bg1.className = "background-image1";
      bg1.alt = "Background Image";
      // 创建 bg2 元素
      const bg2 = document.createElement("img");
      bg2.id = "bg2";
      bg2.className = "background-image2";
      bg2.alt = "Background Image";
      // 将元素插入到容器的开头
      container.insertBefore(bg2, container.firstChild);
      container.insertBefore(bg1, container.firstChild);
    },

    /**
     * 初始化背景图
     * @param {string} configKey - 用户配置中的背景图配置键名
     * @param {string} containerSelector - 容器选择器，用于设置透明背景
     * @param {boolean} useDefaultBackground - 是否使用默认背景图
     */
    async initializeBackground(configKey, containerSelector = ".login", useDefaultBackground = false) {
      // 保存初始化参数，用于主题切换时重新初始化
      this.backgroundInitParams = {
        configKey,
        containerSelector,
        useDefaultBackground,
      };
      // 如果需要自动创建元素且元素不存在，则创建
      const container = document.querySelector(containerSelector);
      if (container && (!document.getElementById("bg1") || !document.getElementById("bg2"))) {
        this.createBackgroundElements(container);
      }

      const bg1 = document.getElementById("bg1");
      const bg2 = document.getElementById("bg2");
      if (!bg1 || !bg2) {
        console.warn("背景图元素 #bg1 或 #bg2 未找到，请确保页面中包含这些元素");
        return;
      }

      const backgroundConfig = this.storeUserConfig?.[configKey];
      if (backgroundConfig === "bing") {
        await this.setupBingWallpaper(bg1, bg2, container);
      } else if (Array.isArray(backgroundConfig) && backgroundConfig.length > 1) {
        this.setupCustomWallpaperCarousel(bg1, bg2, backgroundConfig, container);
      } else if (Array.isArray(backgroundConfig) && backgroundConfig.length === 1) {
        this.setupSingleCustomWallpaper(bg1, backgroundConfig[0], container);
      } else if (useDefaultBackground) {
        this.setupDefaultWallpaper(bg1, container);
      }
    },

    /**
     * 设置 Bing 壁纸轮播
     */
    async setupBingWallpaper(bg1, bg2, container) {
      if (!this.storeBingWallPapers) {
        await this.$store.dispatch("base/fetchBingWallPapers");
      }
      if (this.storeBingWallPapers.length === 0) return;
      this.loadBackgroundImage(bg1, this.storeBingWallPapers[this.bingWallPaperIndex], container, true);
      this.backgroundInterval = setInterval(() => {
        this.switchWallpaper(bg1, bg2, this.storeBingWallPapers);
      }, this.bkInterval);
    },

    /**
     * 设置自定义壁纸轮播
     */
    setupCustomWallpaperCarousel(bg1, bg2, wallpapers, container) {
      this.loadBackgroundImage(bg1, wallpapers[this.customWallPaperIndex], container, true);

      this.backgroundInterval = setInterval(() => {
        this.switchWallpaper(bg1, bg2, wallpapers);
      }, this.bkInterval);
    },

    /**
     * 设置单张自定义壁纸
     */
    setupSingleCustomWallpaper(bg1, wallpaperUrl, container) {
      this.loadBackgroundImage(bg1, wallpaperUrl, container);
    },

    /**
     * 设置默认壁纸
     */
    setupDefaultWallpaper(bg1, container) {
      // 根据当前深色模式状态选择背景图
      const isDark = this.useDarkMode;
      const defaultImage = isDark
        ? require("@/assets/background-dark.webp")
        : require("@/assets/background-light.webp");

      this.loadBackgroundImage(bg1, defaultImage, container);
    },

    /**
     * 加载背景图片
     */
    loadBackgroundImage(imgElement, imageSrc, container, useScale) {
      imgElement.src = imageSrc;
      imgElement.onload = () => {
        // 先设置初始状态
        imgElement.style.opacity = 1;
        imgElement.style.transform = "scale(1)";

        if (useScale) {
          requestAnimationFrame(() => {
            imgElement.style.transform = "scale(1.25)";
          });
        }

        // 设置容器背景为透明
        if (container) {
          container.style.background = "transparent";
        }
      };
    },

    /**
     * 切换自定义壁纸
     */
    switchWallpaper(bg1, bg2, wallpapers) {
      if (wallpapers.length === 0) return;
      const curBg = bg1.style.opacity != 0 ? bg1 : bg2;
      const nextBg = bg1.style.opacity != 0 ? bg2 : bg1;

      curBg.style.opacity = 0;
      curBg.style.transform = "scale(1)";
      this.customWallPaperIndex = (this.customWallPaperIndex + 1) % wallpapers.length;

      nextBg.src = wallpapers[this.customWallPaperIndex];
      nextBg.onload = () => {
        nextBg.style.opacity = this.bkOpacity;
        nextBg.style.transform = "scale(1.25)";
      };
    },

    /**
     * 清除背景轮播定时器
     */
    clearBackgroundInterval() {
      if (this.backgroundInterval) {
        clearInterval(this.backgroundInterval);
        this.backgroundInterval = null;
      }
    },

    /**
     * 清除背景图片显示（带过渡效果）
     * @param {boolean} immediate - 是否立即清除，不使用过渡效果
     */
    async clearBackgroundImages(immediate = false) {
      const bg1 = document.getElementById("bg1");
      const bg2 = document.getElementById("bg2");

      await Promise.all(
        [bg1, bg2].map((bg) => {
          if (!bg) return;
          if (immediate) {
            bg.style.transition = "none";
            bg.style.transform = "scale(1)";
            bg.style.opacity = 0;
            bg.src = "";
            requestAnimationFrame(() => {
              bg.style.transition = `opacity 0.8s cubic-bezier(0.42, 0, 0.58, 1), transform ${
                this.bkInterval / 1000
              }s cubic-bezier(0.42, 0, 0.58, 1)`;
            });
          } else {
            bg.style.opacity = 0;
            bg.style.transform = "scale(1)";
            return new Promise((resolve) => {
              setTimeout(() => {
                bg.src = "";
                bg.style.transition = "none";
                requestAnimationFrame(() => {
                  bg.style.transition = `opacity 0.8s cubic-bezier(0.42, 0, 0.58, 1), transform ${
                    this.bkInterval / 1000
                  }s cubic-bezier(0.42, 0, 0.58, 1)`;
                  resolve();
                });
              }, 800);
            });
          }
        })
      );
    },

    /**
     * 平滑主题切换（用于响应主题模式变化）
     * @param {string} configKey - 用户配置中的背景图配置键名
     * @param {string} containerSelector - 容器选择器
     * @param {boolean} useDefaultBackground - 是否使用默认背景图
     */
    async smoothThemeTransition(configKey, containerSelector = ".login", useDefaultBackground = false) {
      this.isChanging = true; // 设置标志，防止重复触发
      const backgroundConfig = this.storeUserConfig?.[configKey];

      // 如果不是默认背景配置，则直接重新初始化（自定义背景不受主题影响）
      if (!useDefaultBackground || backgroundConfig !== undefined) {
        await this.reinitializeBackground(configKey, containerSelector, useDefaultBackground);
        return;
      }

      // 对于默认背景，执行平滑过渡
      const bg1 = document.getElementById("bg1");
      const bg2 = document.getElementById("bg2");

      if (!bg1 || !bg2) {
        // 如果背景元素不存在，直接重新初始化
        await this.reinitializeBackground(configKey, containerSelector, useDefaultBackground);
        return;
      }

      // 确定当前显示的背景和下一个背景
      const currentBg = bg1.style.opacity != 0 ? bg1 : bg2;
      const nextBg = bg1.style.opacity != 0 ? bg2 : bg1;

      // 获取新主题对应的背景图
      const isDark = this.useDarkMode;
      const newThemeImage = isDark
        ? require("@/assets/background-dark.webp")
        : require("@/assets/background-light.webp");

      // 预加载新背景图
      await new Promise((resolve, reject) => {
        nextBg.src = newThemeImage;
        nextBg.onload = () => {
          currentBg.style.opacity = 0;
          nextBg.style.opacity = this.bkOpacity;
          resolve();
        };
        const container = document.querySelector(containerSelector);
        if (container) container.style.background = "transparent";
      });
    },

    /**
     * 重新初始化背景图（用于响应配置变化）
     */
    async reinitializeBackground(configKey, containerSelector = ".login", useDefaultBackground = false) {
      this.clearBackgroundInterval();
      await this.clearBackgroundImages(false);
      this.bingWallPaperIndex = 0;
      this.customWallPaperIndex = 0;
      await this.initializeBackground(configKey, containerSelector, useDefaultBackground);
    },
  },
};
